*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    /* // This defines what 1rem is */
    font-size: 62.5%;
    /* //1 rem = 10px; 10px/16px = 62.5% */
}

body {
    box-sizing: border-box;
    padding: 3rem;
    font-family: sans-serif;
    font-size: 1.7rem;
}

.header {
    display: flex;
    justify-content: space-between;
    /* max-width: 1200px; */
    padding: 1.5rem 3rem;
    background-color: orangered;
    margin-bottom: 1rem;
}

.header a {
    text-decoration: none;
    color: #fff;
}
/* .computerTable {
    color: orangered;
} */

.computerTable * {
    padding: .5rem 1rem;
}

.computerTable thead {
    text-align: left;
    text-transform: capitalize;
}


.computerTable tbody tr:nth-child(even) {
    background-color: #eee;
}

.computerTable tbody tr:nth-child(odd) {
    background-color: #fff;
}
